'use client'

import { useState, useEffect } from 'react'
import QueryDetailsModal from '@/components/QueryDetailsModal'
import type { Query, QueryType, QueryAnalytics, QueriesListResponse } from '@/types/query'

export default function QueryHistoryPage() {
  const [allQueries, setAllQueries] = useState<Query[]>([])
  const [filteredQueries, setFilteredQueries] = useState<Query[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [selectedQuery, setSelectedQuery] = useState<Query | null>(null)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [filter, setFilter] = useState<QueryType | 'all'>('all')
  const [analytics, setAnalytics] = useState<QueryAnalytics | null>(null)

  // Fetch all queries once on mount
  useEffect(() => {
    fetchAllQueries()
  }, [])

  // Filter queries when filter changes
  useEffect(() => {
    if (allQueries.length > 0) {
      applyFilter()
    }
  }, [filter, allQueries])

  // Fetch all queries from the database (no filter)
  const fetchAllQueries = async () => {
    setLoading(true)
    setError(null)

    try {
      const response = await fetch('/api/queries?limit=100')
      const data: QueriesListResponse = await response.json()

      if (!response.ok || !data.success) {
        throw new Error(data.error || 'Failed to fetch queries')
      }

      // Store all queries for count calculations
      setAllQueries(data.queries)
      // Calculate analytics from ALL queries
      calculateAnalytics(data.queries)
      // Apply current filter for display
      applyFilterToQueries(data.queries, filter)
    } catch (err) {
      console.error('Error fetching queries:', err)
      setError(err instanceof Error ? err.message : 'Failed to load query history')
    } finally {
      setLoading(false)
    }
  }

  // Apply filter to queries for display (client-side filtering)
  const applyFilter = () => {
    applyFilterToQueries(allQueries, filter)
  }

  // Helper function to filter queries
  const applyFilterToQueries = (queries: Query[], filterType: QueryType | 'all') => {
    if (filterType === 'all') {
      setFilteredQueries(queries)
    } else {
      const filtered = queries.filter(q => q.query_type === filterType)
      setFilteredQueries(filtered)
    }
  }

  const calculateAnalytics = (queryList: Query[]) => {
    if (queryList.length === 0) {
      setAnalytics(null)
      return
    }

    const typeCounts: Record<QueryType, number> = {
      'AI Prediction - Player Prop': 0,
      'AI Prediction - Game Outcome': 0,
      'Player Stats': 0,
      'Player Comparison': 0
    }

    queryList.forEach(q => {
      typeCounts[q.query_type] = (typeCounts[q.query_type] || 0) + 1
    })

    const mostCommonType = Object.entries(typeCounts)
      .reduce((a, b) => (b[1] > a[1] ? b : a))[0] as QueryType

    const mostRecentDate = queryList[0]?.created_at

    setAnalytics({
      totalQueries: queryList.length,
      mostRecentDate,
      mostCommonType,
      queryTypeCounts: typeCounts
    })
  }

  const handleViewDetails = (query: Query) => {
    setSelectedQuery(query)
    setIsModalOpen(true)
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffMs = now.getTime() - date.getTime()
    const diffMins = Math.floor(diffMs / 60000)
    const diffHours = Math.floor(diffMs / 3600000)
    const diffDays = Math.floor(diffMs / 86400000)

    if (diffMins < 1) return 'Just now'
    if (diffMins < 60) return `${diffMins} min${diffMins > 1 ? 's' : ''} ago`
    if (diffHours < 24) return `${diffHours} hour${diffHours > 1 ? 's' : ''} ago`
    if (diffDays < 7) return `${diffDays} day${diffDays > 1 ? 's' : ''} ago`

    return date.toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      year: date.getFullYear() !== now.getFullYear() ? 'numeric' : undefined
    })
  }

  const getQueryTypeColor = (type: QueryType) => {
    switch (type) {
      case 'AI Prediction - Player Prop':
        return 'bg-primary/100 text-blue-800 border-blue-200'
      case 'AI Prediction - Game Outcome':
        return 'bg-purple-100 text-purple-800 border-purple-200'
      case 'Player Stats':
        return 'bg-green-100 text-foreground border-border/50'
      case 'Player Comparison':
        return 'bg-orange-100 text-orange-800 border-orange-200'
    }
  }

  const getQueryIcon = (type: QueryType) => {
    switch (type) {
      case 'AI Prediction - Player Prop':
        return 'üéØ'
      case 'AI Prediction - Game Outcome':
        return 'üèÜ'
      case 'Player Stats':
        return 'üìä'
      case 'Player Comparison':
        return '‚öñÔ∏è'
    }
  }

  const getQuerySummary = (query: Query): string => {
    switch (query.query_type) {
      case 'AI Prediction - Player Prop':
        const propDetails = query.details as any
        return `${propDetails.playerName} - ${propDetails.overUnder} ${propDetails.threshold} ${propDetails.propType}`
      case 'AI Prediction - Game Outcome':
        const gameDetails = query.details as any
        return `${gameDetails.team1} vs ${gameDetails.team2}`
      case 'Player Stats':
        const statsDetails = query.details as any
        return `${statsDetails.playerName} (${statsDetails.team})`
      case 'Player Comparison':
        const compDetails = query.details as any
        return `${compDetails.player1.name} vs ${compDetails.player2.name}`
      default:
        return 'Unknown query'
    }
  }

  const filterButtons: Array<{ value: QueryType | 'all'; label: string }> = [
    { value: 'all', label: 'All' },
    { value: 'AI Prediction - Player Prop', label: 'Player Props' },
    { value: 'AI Prediction - Game Outcome', label: 'Game Predictions' },
    { value: 'Player Stats', label: 'Player Stats' },
    { value: 'Player Comparison', label: 'Comparisons' }
  ]

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-16 w-16 border-b-4 border-blue-600 mx-auto mb-4"></div>
          <p className="text-muted-foreground">Loading your query history...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-2xl font-bold text-foreground">Query History</h1>
        <p className="text-muted-foreground mt-1">View all your past predictions, searches, and comparisons</p>
      </div>

      {/* Analytics Summary */}
      {analytics && (
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-6">
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 bg-primary/100 rounded-lg flex items-center justify-center">
                <span className="text-2xl">üìà</span>
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Total Queries</p>
                <p className="text-2xl font-bold text-foreground">{analytics.totalQueries}</p>
              </div>
            </div>
          </div>

          <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-6">
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 bg-purple-100 rounded-lg flex items-center justify-center">
                <span className="text-2xl">üïê</span>
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Most Recent</p>
                <p className="text-lg font-semibold text-foreground">
                  {formatDate(analytics.mostRecentDate!)}
                </p>
              </div>
            </div>
          </div>

          <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-6">
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 bg-green-100 rounded-lg flex items-center justify-center">
                <span className="text-2xl">{getQueryIcon(analytics.mostCommonType!)}</span>
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Most Common</p>
                <p className="text-sm font-semibold text-foreground">
                  {analytics.mostCommonType === 'AI Prediction - Player Prop' ? 'Player Props' :
                   analytics.mostCommonType === 'AI Prediction - Game Outcome' ? 'Game Predictions' :
                   analytics.mostCommonType === 'Player Comparison' ? 'Comparisons' : 'Player Stats'}
                </p>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Filter Buttons */}
      <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-4">
        <div className="flex flex-wrap gap-2">
          {filterButtons.map((btn) => (
            <button
              key={btn.value}
              onClick={() => setFilter(btn.value)}
              className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                filter === btn.value
                  ? 'bg-blue-600 text-white'
                  : 'bg-secondary text-muted-foreground hover:bg-secondary/300'
              }`}
            >
              {btn.label}
              {btn.value !== 'all' && analytics && (
                <span className="ml-2 text-xs opacity-75">
                  ({analytics.queryTypeCounts[btn.value as QueryType] || 0})
                </span>
              )}
            </button>
          ))}
        </div>
      </div>

      {/* Error State */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4">
          <p className="text-red-800">‚ö†Ô∏è {error}</p>
          <button
            onClick={fetchAllQueries}
            className="mt-2 text-sm text-red-600 hover:text-red-700 font-medium underline"
          >
            Try again
          </button>
        </div>
      )}

      {/* Queries List */}
      {!error && filteredQueries.length === 0 ? (
        <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-12 text-center">
          <div className="w-20 h-20 bg-secondary rounded-full flex items-center justify-center mx-auto mb-4">
            <span className="text-4xl">üìã</span>
          </div>
          <h3 className="text-xl font-semibold text-foreground mb-2">No queries yet</h3>
          <p className="text-muted-foreground mb-6">
            Start using AI Predictions, Player Stats, or Player Comparison to see your history here
          </p>
        </div>
      ) : (
        <div className="grid grid-cols-1 gap-4">
          {filteredQueries.map((query) => (
            <div
              key={query.id}
              className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 hover:shadow-lg transition-shadow p-5 border-l-4 border-blue-500"
            >
              <div className="flex items-start justify-between gap-4">
                <div className="flex-1">
                  {/* Query Type Badge */}
                  <span className={`inline-block px-3 py-1 rounded-full text-xs font-semibold border ${getQueryTypeColor(query.query_type)} mb-3`}>
                    {getQueryIcon(query.query_type)} {query.query_type}
                  </span>

                  {/* Query Summary */}
                  <h3 className="text-lg font-semibold text-foreground mb-2">
                    {getQuerySummary(query)}
                  </h3>

                  {/* Timestamp */}
                  <p className="text-sm text-muted-foreground">
                    {formatDate(query.created_at)}
                  </p>
                </div>

                {/* View Details Button */}
                <button
                  onClick={() => handleViewDetails(query)}
                  className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors font-medium whitespace-nowrap"
                >
                  View Details
                </button>
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Details Modal */}
      <QueryDetailsModal
        query={selectedQuery}
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false)
          setSelectedQuery(null)
        }}
      />
    </div>
  )
}
